.category-cards-container {
	box-sizing: border-box;
	position: relative;
	overflow-y: hidden;
	width: 100%;
}
.category-cards-wrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	padding: 0;
	overflow: hidden;
	overflow-x: scroll;
	white-space: nowrap;
	margin-bottom: -1.1rem;
	width: 100%;
	box-sizing: border-box;
}
.category-card-link {
	display: block;
	min-width: 160px;
	max-width: 160px;
	margin-right: 1rem;
	z-index: auto;
	margin-bottom: 1.1rem;
	border: 1px solid #e6e6e6;
	text-decoration: none;
}
.category-card-link:last-of-type {
	margin-right: 0;
}

.category-card-image-container {
	height: 160px;
}

.category-card-image {
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.category-toggle .category-card-image-container {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

.category-card-image-container .bi-three-dots {
	font-size: 3rem;
	color: #c64f24;
}

.category-card-title-container {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	padding: 0.5rem;
	height: 50px;
}

.category-card-title {
	font-size: 0.9rem;
	font-weight: bold;
	overflow-wrap: break-word;
}

.more-categories-container {
	display: none;
}
.category-toggle {
	display: none;
}

@media screen and (min-width: 769px) {
	.category-cards-wrapper {
		display: grid;
		grid-template-columns: repeat(4, 23.75%);
		grid-template-rows: auto auto;
		grid-column-gap: 1.66%;
		-webkit-column-gap: 1.66%;
		        column-gap: 1.66%;
		grid-row-gap: 1.66%;
		row-gap: 1.66%;
		padding-bottom: 1.1rem;
	}
	.category-card-link {
		display: block;
		min-width: 100%;
		max-width: auto;
		margin-right: 0;
		z-index: auto;
		margin-bottom: 0;
		border: 1px solid #e6e6e6;
		text-decoration: none;
		font-weight: bold;
	}

	.category-card-link:hover {
		box-shadow: 0 1px 3px 1px rgba(198, 79, 36, 0.5);
	}

	.category-toggle {
		display: block;
	}
	.category-toggle .category-card-title {
		color: #c64f24;
	}

	.more-categories-container {
		display: grid;
		grid-template-columns: repeat(4, 23.75%);

		grid-column-gap: 1.66%;

		-webkit-column-gap: 1.66%;

		        column-gap: 1.66%;
		border-top: 1px solid #ccc;
		margin-top: 0rem;
		padding-top: 0rem;
		opacity: 0;
		max-height: 0px;
		transition: padding-top 200ms ease-in-out, margin-top 200ms ease-in-out,
			max-height 200ms ease-in-out, opacity 200ms ease-in-out;
	}
	.transition {
		margin-top: 2rem;
		padding-top: 2rem;
		max-height: 250px;
		opacity: 1;
	}

	.more-categories-container-link {
		text-decoration: none;
		font-weight: bold;
		padding: 0.5rem;
	}

	.more-categories-container-link:hover {
		text-decoration: underline;
	}
}

:root {
	--link-color: #c64f24;
	--text-color: #000;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
*:focus {
	color: #212529;
	border-color: #86b7fe;
	outline: 0;
	box-shadow: 0 0 0 0.25rem rgb(13 110 253 / 25%);
}

html,
body {
	background-color: #f9fafb;
}
ul,
ol {
	margin: 0;
	padding: 0;
	list-style: none;
}

.container {
	max-width: 960px;
	margin-left: auto;
	margin-right: auto;
	padding-left: 0.75rem;
	padding-right: 0.75rem;
}

h1,
h2,
h3,
h4,
h5,
h5 {
	font-family: "Open Sans", sans-serif;
}
p,
a,
form {
	font-family: "Lato", sans-serif;
}

a {
	display: block;
}

a:link,
a:visited {
	color: #c64f24;
	color: var(--link-color);
}

/* a:visited {
	color: rgb(92, 67, 0);
} */
a:active,
a:hover {
	color: #c64f24;
	color: var(--link-color);
}

